#main{
  box-sizing: border-box;
  float: left;
  max-width: 90%;
  padding: 40px 20% 0 20%;
  /* padding:40px 25%; */
}

.bodyText{
  margin-bottom: 40px;
}

.App{
  display: flex;
}

.tags{
  font-size: 18px;
}



.carousel{
  height: 60%;
}

.carousel-inner{
  height: 100%;
}

.carousel-item{
  height: 100%;
}

.projectImg{
  box-sizing: border-box;
  width: 60%;
  min-width: 280px;
  border: 3px solid #AD9C70;
  /* min-width: 50%; */
}

.carouselBox{
  border: 3px solid #AD9C70;
}

.carouselImg{
  width: 60%;
}

.navlink{
  text-decoration: none;
}

@media (max-width: 600px){
  .projectImg{
    width: 280px;
  }

  #main{
    padding: 40px 5%;
  }
}

a{
  overflow-wrap: break-word;
}
#wrapper{
    position: fixed;
    z-index: 1
}

#navBar{
    position: fixed;
    padding: 20px 40px 20px 40px;
    font-size: 24px;
    line-height: 48px;
    background-color: #00352D;
    border-right: 4px solid #AD9C70;
    height: 100%;
    box-shadow:0px 5px 10px grey;  
}

.hide{
    left: -240px;
    -webkit-transition: left 0.3s linear;
    transition: left 0.3s linear;
}

.show{
    left: 0px;
    -webkit-transition: left 0.3s linear;
    transition: left 0.3s linear;
}

.fa-bars{
    color: #AD9C70;
  }
.fa-bars:hover{
    cursor: pointer;
    color: white;
}
.fa-circle{
    position: relative;
    box-sizing: border-box;
    font-size: 8px;
    bottom: 2px;
}

.navElement{
    color: #AD9C70;
}
.subElement{
    padding-left: 12px;
    font-size: 16px;
    color: #AD9C70;
    line-height: 32px;
}
.navElement:hover{
    color: white;
    cursor: pointer;
}

.subElement:hover{
    color: white;
    cursor: pointer;
}

#email{
    color: #AD9C70;
    font-size: 12px;
}

#navname{
    padding-top: 5px;
    color: #AD9C70;
    font-size: 18px; 
}

.iconGroup{
    line-height: 12px;
    position: absolute;
    bottom: 24px;
    left: 10px;
}

.fixed{
    font-size: 24px;
    position: fixed;
    left: 36px;
    top: 32px;
    background-color: #00352D;
    padding: 4px;
}


#selfphoto{
    float:left;
    height: 200px;
    margin: 0 20px 10px 0;
}


.carousel .item .carousel-item {
    height: 300px;
  }
  
  .item img {
      position: absolute;
      top: 0;
      left: 0;
      min-height: 300px;
  }


.homeworkContainer{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}
.label{position: relative;
    width: 240px;
    left: 0px;
    bottom: -220px;
    text-align: center;
    padding: 2px 0px;
    margin: 0px;
    background-color: #00352D;
    color: #AD9C70;
    font-size: 20px;
}

.box > a{
    text-decoration: none;
}

